body {
  margin: 0;
  font-family: monospace;
  background-color: white;
  color: black;
}

.App {
  text-align: center;
}

h1 {
  margin: 0;
  padding: 10px;
  font-size: 24px;
}

.health-indicator {
  margin: 10px;
  font-size: 16px;
}

.ascii-art {
  text-align: center;
  margin-top: 20px;
  font-family: monospace;
  white-space: pre;
}

button {
  margin: 10px;
  padding: 10px 20px;
  background-color: transparent;
  color: black;
  border: none;
  cursor: pointer;
  font-size: 16px;
}

button:hover {
  background-color: rgba(0, 0, 0, 0.1);
}

p {
  margin: 10px;
}

.message-log {
  position: fixed;
  right: 0;
  top: 0;
  width: 200px;
  height: 100vh;
  padding: 10px;
  overflow-y: hidden; /* Disable manual scrolling */
  font-size: 14px;
  background-color: rgba(255, 255, 255, 0.9); /* Semi-transparent background */
  border-left: 1px solid #ccc;
}

.message {
  margin: 5px 0;
  opacity: 1;
  transition: opacity 1s ease-out; /* Fade-out effect */
}

.message.fade {
  opacity: 0;
}

.tabs {
  display: flex;
  justify-content: center;
  margin-top: 10px;
}

.tabs button {
  margin: 0 5px;
  padding: 5px 10px;
  background-color: transparent;
  color: black;
  border: none;
  cursor: pointer;
  font-size: 16px;
}

.tabs button.active {
  border-bottom: 2px solid black;
}

.location-buttons {
  display: flex;
  justify-content: center;
  margin-top: 10px;
}

.location-buttons button {
  margin: 0 5px;
  padding: 5px 10px;
  background-color: transparent;
  color: black;
  border: none;
  cursor: pointer;
  font-size: 16px;
}

.location-buttons button.active {
  border-bottom: 2px solid black;
}

.equipment-tab {
  margin-top: 20px;
  text-align: left;
  padding: 0 20px;
}

.equipment-tab h2 {
  font-size: 20px;
  margin-bottom: 10px;
}

.equipment-tab p {
  margin: 5px 0;
}

.equipment-tab ul {
  list-style-type: none;
  padding: 0;
}

.equipment-tab li {
  margin: 10px 0;
}

.equipment-tab button {
  margin-left: 10px;
  padding: 5px 10px;
  background-color: transparent;
  color: black;
  border: 1px solid black;
  cursor: pointer;
  font-size: 14px;
}

.equipment-tab button:disabled {
  opacity: 0.5;
  cursor: not-allowed;
}
